  border-right: 0.5rem solid #6f5eaa;
  padding-bottom: 1rem; }

.title {
  margin: 0 0 2rem 53%; }

body {
  background: #e8dddd;
  color: #151a17;
  font-family: "Roboto", sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
