.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.pageRow {
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-top: 10rem; }

.pageCol {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin-top: 10rem; }

.flexContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-top: 10rem; }

.flexCol {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin: 3rem; }

.flexRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin: 3rem; }

.bioPic {
  height: auto;
  max-height: 55rem;
  width: auto;
  box-shadow: 0 0.2rem 0.4rem 0 #151a17; }

.card {
  box-shadow: 0 0.2rem 0.4rem 0 #151a17;
  padding: 1rem;
  margin: 5rem;
  opacity: 0.85; }

.jumbotron {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
